
main {
	width: 400px;
	#calculator_power {
		padding: 0 10%;
		width: 100%;
		border: 1px solid black;
		text-align: center;
		h1 { font-size: 28px; }
		.row {
			.input_field {
				position: relative;
				input[type="number"] {
					width: 45%;
					padding: 7px 5px;
					margin: 10px 0 15px;
					box-sizing: border-box;
					padding-right: 30px;
					-moz-appearance: textfield;
					&::-webkit-inner-spin-button, 
					&::-webkit-outer-spin-button { 
				  		-webkit-appearance: none; 
				  		margin: 0; 
					}
				}
				input[type="radio"] {
					position: absolute;
				}
				span {
					color: gray;
					position: absolute;
				    top: 32%;
				    right: 29%;
				}
			}
			label {
				font-weight: 600;
				display: block;
				&[for="units_metre"], &[for="units_feet"] {
					display: inline-block;
					position: absolute;
					top: -5px;
				}
				&[for="units_feet"] {
					right: 75px;
				}
				&[for="units_metre"] {
					right: 35px;
				}
			}
			#units_feet, #units_metre {
				position: absolute;
				top: 17px;
			}
			#units_feet {
				right: 70px;
			}
			#units_metre {
				right: 32px;
			}
			button {
				background: white;
				border: 2px solid black;
				padding: 5px 15px;
			}
			&:first-of-type input[type="number"] {
				padding-right: 80px;
			}
		}
		.row.calc_result-div {
			text-align: left;
			h2, p {
				display: inline-block;
			}
			h2 { margin-right: 10px; }
			p { opacity: 1 }
		}
	}
}